.progress-bar-container {
    position: relative;
    width: 100px;
    height: 3px;
    border-radius: 5px;
    border: 2px solid #272727;
    overflow: hidden;
    margin: 0px;
}

.filler {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: #2196F3;
}

.progress-label {
    /* text-align: center; */
    margin-left: 35px;
    font-weight: bold;
}
