.PointStatus-container {
    width: 200px;
    min-width: 200px;
    margin-bottom: 10px;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    align-items: center;
    justify-content: center;
    background-color: white;
    cursor: pointer;
}

.PointStatus-container:hover {
    background-color: #2196F3;
    color: white;
}
.PointStatus-container:hover .PointStatus-bar-progress{
    background: #FF9800;
}

.PointStatus-container p {
    width: 45%;
    display: flex;
    justify-content: center;
    margin: 10px 0px 3px 0px;
    font-size: 1.1rem;
}

.PointStatus-container p:nth-child(1){
    justify-content: flex-start;
}

.PointStatus-container p:nth-child(2) span{
    font-weight: bold;
}

.PointStatus-bar {
    width: 200px;
    margin: 0px;
}

.PointStatus-bar-progress{
    height: 5px;
    background: #2196F3;
}

